import React, { useState, useEffect } from "react";
import {
    Box,
    Paper,
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Modal,
    List,
} from "@mui/material";
import {
    ItemPara,
    StyTableCell,
    YearlyBtn,
    StyButtonDownload,
    LightTooltip,
    style,
    StyTextFieldLeave,
    DltBtn,
    ListItem
} from "../css/styles";
import Calendar from 'react-calendar';
import moment from 'moment';
// import '../css/calendar.css'
import swal from "sweetalert";
import 'react-calendar/dist/Calendar.css';
import "../css/profile.css"
import jwt_decode from 'jwt-decode';
import Row from 'react-bootstrap/Row';
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";


function EmploeeAttendanceHistory({ product }) {
    const [date, setDate] = useState(new Date())
    const [historyData, seHistoryData] = useState([])
    const _data = JSON.parse(localStorage.getItem('data'))

    const onCalendarClicked = async (e) => {
        const data_decoded = jwt_decode(_data)
        const dd = moment(date).format("YYYY-MM-DD")

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/history/id"
            : "/api/attendance/history/id";

        await fetch(URL, {
            method: "POST",
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "emp_id": data_decoded.user.emp_id,
                "date": dd,
                "tokens": _data
            }),
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const result = await response.json();
                        seHistoryData(result.data)
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }
    useEffect(() => {
        onCalendarClicked()
    }, [])



    return (
        <div style={{ marginTop: "12%" }}>
            <center>
                <h5 className="header">Select the Date</h5>
                <div className="calendar-container">
                    <Calendar onChange={setDate}
                        value={date}
                        onClickDay={onCalendarClicked}
                        maxDate={new Date()}
                    />
                </div>
                <p className="header">Double Click the Date</p>
            </center>


            <div>
                <ItemPara>
                    <center style={{ fontWeight: "bold" }}>Attendance detail:</center>
                </ItemPara>
            </div>
            {historyData.length !== 0 ? <Row>
                {historyData.map((history, index) => (
                    <Card
                        key={history._id}
                        style={{
                            marginTop: "10px",
                            width: "100%",
                            backgroundColor: "cyan",
                        }}
                    >
                        <div style={{ padding: "0%" }}>
                            {/* <h4 style={{ flex: 1, justifyContent: "center", alignItems: "center", marginTop: "10px" }}>{history.index + 1}</h4> */}
                            <CardContent>
                                <ListItem style={{
                                    fontWeight: "600",
                                    margin: "0px"
                                }}>
                                    Sign In: {history.in_time ? history.in_time : "-"}
                                </ListItem>
                            </CardContent>
                            <CardContent>
                                <ListItem style={{
                                    fontWeight: "600"
                                }}>
                                    Sign Out: {history.out_time ? history.out_time : "-"}
                                </ListItem>
                            </CardContent>
                            <CardContent>
                                <ListItem style={{
                                    fontWeight: "600"
                                }}>
                                    Distance In: {history.distance_in ? history.distance_in : "-"}
                                </ListItem>
                            </CardContent>
                            <CardContent>
                                <ListItem style={{
                                    fontWeight: "600"
                                }}>
                                    Distance Out: {history.distance_out ? history.distance_out : "-"}
                                </ListItem>
                            </CardContent>
                            <CardContent>
                                <ListItem style={{
                                    fontWeight: "600"
                                }}>
                                    Activity: {history.activity ? history.activity : "-"}
                                </ListItem>
                            </CardContent>
                            <CardContent>
                                <ListItem style={{
                                    fontWeight: "600"
                                }}>
                                    Time: {history.total_time ? history.total_time : "-"}
                                </ListItem>
                            </CardContent>
                        </div>
                    </Card>
                ))}
            </Row> : <center style={{ marginTop: "5px" }}><h4>No Data</h4></center>}



        </div>
    );
}

export default EmploeeAttendanceHistory;