import React, { useState, useEffect } from "react";
import {
    Box,
    Paper,
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Modal,
} from "@mui/material";
import {
    ItemPara,
    StyTableCell,
    YearlyBtn,
    RoundButton,
    StyButtonDownload,
    LightTooltip,
    style,
    StyTextFieldLeave,
    DltBtn,
} from "../css/styles";
import Calendar from 'react-calendar';
import moment from 'moment';
// import '../css/calendar.css'
import swal from "sweetalert";
import 'react-calendar/dist/Calendar.css';
import "../css/profile.css"
import jwt_decode from 'jwt-decode';
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";


function EmploeeAttendance(props) {

    const [distance, setDistance] = useState("")
    const [timeToDisplay, setTimeToDisplay] = useState("")
    const [dataHistory, setDataHistory] = useState("")

    const [dateDisplay, setDateDisplay] = useState("")

    //Register In
    const signInButton = () => {
        // geo location
        if (navigator.geolocation) {
            navigator.permissions
                .query({ name: "geolocation" })
                .then(function (result) {
                    if (result.state === "granted") {
                        console.log(result.state);
                        //If granted then you can directly call your function here
                        // navigator.geolocation.getCurrentPosition(success);
                        navigator.geolocation.getCurrentPosition(function (position) {
                            console.log("Latitude is :", position.coords.latitude);
                            console.log("Longitude is :", position.coords.longitude);
                            const dis = calculateDistance(position.coords.latitude, position.coords.longitude, 20.15592954558147, 20.78765008200381)
                            setDistance(dis)
                        });
                    } else if (result.state === "prompt") {
                        navigator.geolocation.getCurrentPosition(success, errors);
                    } else if (result.state === "denied") {
                        alert("You have denied the Location Access!")
                    }
                    result.onchange = function () {
                        console.log(result.state);
                    };
                });
        } else {
            alert("Sorry Not available!");
        }

        // // now check for distance
        // if (distance !== "") {
        //     if (textValue === "Sign In") {
        //         signInBackendCall()
        //     }

        //     if (textValue === "Sign Out") {
        //         signOutBackendCall()
        //     }
        // }
    }
    const _data = JSON.parse(localStorage.getItem('data'))

    //function for sign in backend
    const signInBackendCall = (e) => {
        // e.preventDefault()
        signInButton()

        if (distance !== "") {
            swal(`You are ${distance} mtrs away!`, {
                buttons: ["Oh no!", true],
            }).then(async (value) => {
                if (value === true) {
                    try {
                        const data_decoded = jwt_decode(_data)
                        const time = moment().format("LTS")
                        // setTimeToDisplay(time)

                        const URL = window.location.href.startsWith("http://localhost")
                            ? "http://localhost:5000/api/attendance/signin"
                            : "/api/attendance/signin";

                        await fetch(URL, {
                            method: "POST",
                            headers: {
                                "Accept": 'application/json',
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({
                                "emp_id": data_decoded.user.emp_id,
                                "empID": data_decoded.user._id,
                                "in_time": time,
                                "distance_in": distance,
                                "tokens": _data
                            }),
                        })
                            .then(async response => {
                                try {
                                    if (response.status !== 200) {
                                        console.log("err")
                                    } else {
                                        swal("Successfully Registered In!")
                                        getHistoryOfUser()
                                    }
                                } catch (err) {
                                    console.log(err);
                                };
                            })
                            .catch(err => {
                                console.log(err);
                            });
                    } catch (err) {
                        console.log(err);
                    }
                }
            });
        }

    }

    //function for sign out backend
    const signOutBackendCall = () => {
        signInButton()

        if (distance !== "") {
            swal(`You are ${distance} mtrs away!`, {
                buttons: ["Oh no!", true],
            }).then(async (value) => {
                if (value === true) {
                    try {
                        const data_decoded = jwt_decode(_data)
                        const time = moment().format("LTS")
                        const date = moment(new Date()).format("YYYY-MM-DD")


                        const URL = window.location.href.startsWith("http://localhost")
                            ? "http://localhost:5000/api/attendance/signout"
                            : "/api/attendance/signout";
                        await fetch(URL, {
                            method: "POST",
                            headers: {
                                "Accept": 'application/json',
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({
                                "emp_id": data_decoded.user.emp_id,
                                "out_time": time,
                                "distance_out": distance,
                                'activity': "Test Activity",
                                "date": date,
                                "tokens": _data
                            }),
                        })
                            .then(async response => {
                                try {
                                    if (response.status !== 200) {
                                        console.log("err")
                                    } else {
                                        swal("Successfully Registered Out!")
                                        getHistoryOfUser()
                                    }
                                } catch (err) {
                                    console.log(err);
                                };
                            })
                            .catch(err => {
                                console.log(err);
                            });
                    } catch (err) {
                        console.log(err);
                    }
                }
            });
        }

    }

    //If access location popup opens
    function success(position) {
        const dis = calculateDistance(position.coords.latitude, position.coords.longitude, position.coords.latitude, position.coords.longitude)
        setDistance(dis)
        // console.log(`More or less ${crd.accuracy} meters.`);
    }

    function errors(err) {
        alert(`ERROR(${err.code}): ${err.message}`);
    }

    //calculate distance
    function calculateDistance(lat1, lon1, lat2, lon2, unit) {
        if ((lat1 === lat2) && (lon1 === lon2)) {
            return 0;
        }
        else {
            var radlat1 = Math.PI * lat1 / 180;
            var radlat2 = Math.PI * lat2 / 180;
            var theta = lon1 - lon2;
            var radtheta = Math.PI * theta / 180;
            var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
            if (dist > 1) {
                dist = 1;
            }
            dist = Math.acos(dist);
            dist = dist * 180 / Math.PI;
            dist = dist * 60 * 1.1515;
            if (unit === "K") { dist = dist * 1.609344 }
            if (unit === "N") { dist = dist * 0.8684 }
            // console.log("distance", Math.trunc(dist));
            return Math.trunc(dist);
        }
    }

    // initially called function to check in and out time for button disability
    const getHistoryOfUser = async () => {
        const data_decoded = jwt_decode(_data)
        const date = moment(new Date()).format("YYYY-MM-DD")


        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/history/id"
            : "/api/attendance/history/id";

        await fetch(URL, {
            method: "POST",
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "emp_id": data_decoded.user.emp_id,
                "date": date,
                "tokens": _data
            }),
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const result = await response.json();
                        console.log(result.data);
                        (result.data.length === 0 ? setDataHistory("") : setDataHistory(result.data[0].out_time));
                        (result.data.length === 0 ? setTimeToDisplay("-") : setTimeToDisplay(result.data[0].in_time));
                        (result.data.length === 0 ? setDistance("-") : setDistance(result.data[0].distance_in));
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });

    }

    useEffect(() => {
        const displayDate = moment(new Date()).format('LL')
        setDateDisplay(displayDate)

        getHistoryOfUser()
    }, [])

    const styles = {
        signInPop: {
            fontSize: dataHistory === "" ? "20px" : dataHistory === null ? "15px" : "20px",
            backgroundColor: 'orange',
            color: "black"
        },
        signOutPop: {
            fontSize: dataHistory === "" ? "15px" : dataHistory === null ? "20px" : "15px",
            backgroundColor: 'red',
            color: "white"
        }
    };

    return (
        <div style={{ marginTop: "7%" }}>
            <center>
                <Card
                    style={{
                        marginTop: "15%",
                        width: "100%",
                        backgroundColor: "cyan",
                    }}
                >
                    <div style={{ padding: "2%" }}>
                        <h4 style={{ flex: 1, justifyContent: "center", alignItems: "center", marginTop: "10px" }}>Welcome</h4>
                        <CardContent>
                            <ItemPara style={{
                                fontWeight: "600"
                            }}>
                                <center>{dateDisplay}</center>
                            </ItemPara>

                        </CardContent>
                    </div>
                </Card>
            </center>

            <Box className="register-button-container"
            // sx={{
            //     flexGrow: 1, flexDirection: "row", justifyContent: "center", alignItems: "center"
            // }}
            >
                <RoundButton
                    style={styles.signInPop}
                    disabled={dataHistory === "" ? false : dataHistory === null ? true : false}
                    // color={dataHistory === "" ? 'orange' : dataHistory === null ? "black" : 'orange'}
                    variant="outlined"
                    onClick={() => {
                        signInBackendCall()
                    }}
                >
                    Sign In
                </RoundButton>
                <RoundButton
                    disabled={dataHistory === "" ? true : dataHistory === null ? false : true}
                    variant="outlined"
                    onClick={() => {
                        signOutBackendCall()
                    }}
                    style={styles.signOutPop}
                >
                    Sign out
                </RoundButton>
            </Box>

            <center>
                <div style={{
                    position: "absolute",
                    bottom: 10,
                    left: 0,
                    flex: 1,
                    width: "100%",
                    display: 'flex',
                    justifyContent: 'center'
                }}>
                    <Card
                        style={{
                            position: "static",
                            backgroundColor: "red",
                            width: "90%",
                        }}
                    >
                        <div style={{ padding: "10px" }}>
                            <h4 style={{ fontWeight: "600", color: "white", marginTop: "10px" }}>Check In Time</h4>
                            <CardContent>
                                <ItemPara style={{
                                    fontWeight: "600"
                                }}>
                                    Sign In:{timeToDisplay === "" ? "-" : ` ${timeToDisplay}`}
                                </ItemPara>
                                <ItemPara style={{
                                    fontWeight: "600",
                                    marginTop: "15px"
                                }}>
                                    Distance:{distance === "" ? "-" : ` ${distance} mtrs`}
                                </ItemPara>
                            </CardContent>
                        </div>
                    </Card>
                </div>
            </center>
        </div>
    );
}

export default EmploeeAttendance;