import React, { useState, useEffect } from "react";
import {
    Box,
    Paper,
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
} from "@mui/material";
import {
    ItemPara,
    StyButton,
    DltBtn,
    YearlyBtn,
    StyTextFieldLeave,
    ListItem
} from "../css/styles";
import jwt_decode from 'jwt-decode';
import Row from 'react-bootstrap/Row';
import Card from "@material-ui/core/Card";
import moment from 'moment';
import CardContent from "@material-ui/core/CardContent";
import swal from "sweetalert";

function EmployeeNotification(props) {

    const [data, setData] = useState([]);
    const [respondVisibility, setRespondVisibility] = useState(false);
    const [userMessage, setUserMessage] = useState("");
    const [attendance_date, setAttendance_date] = useState("");


    const _data = JSON.parse(localStorage.getItem('data'))

    const notificationData = async (e) => {

        const data_decoded = jwt_decode(_data)

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/get/notification"
            : "/api/attendance/get/notification";

        await fetch(URL, {
            method: "POST",
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "_id": data_decoded.user._id,
                "tokens": _data
            }),
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const result = await response.json();
                        console.log(result.data);
                        setData(result.data)
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }

    const UserRespondFunction = async (e) => {
        e.preventDefault();
        const data_decoded = jwt_decode(_data)

        swal("Submit?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                const URL = window.location.href.startsWith("http://localhost")
                    ? "http://localhost:5000/api/attendance/delete/notification"
                    : "/api/attendance/delete/notification";

                await fetch(URL, {
                    method: "POST",
                    headers: {
                        "Accept": 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        "_id": data_decoded.user._id,
                        "attendance_date": attendance_date,
                        "emp_response": userMessage,
                        "tokens": _data
                    }),
                })
                    .then(async response => {
                        try {
                            if (response.status !== 200) {
                                console.log("err")
                            } else {
                                const result = await response.json();
                                console.log(result);
                                setUserMessage()
                                setRespondVisibility(false)
                                notificationData()
                                swal("Successful!")
                            }
                        } catch (err) {
                            console.log(err);
                        };
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }
        })

    }

    useEffect(() => {
        notificationData()
    }, [])
    return (
        <Box sx={{ flexGrow: 1 }} style={{ marginTop: "10%" }}>
            <Grid item xs={12} sm={12} md={9}>
                <ItemPara style={{ fontWeight: "600", fontSize: "18px" }}>
                    <center>Admin Responses to your Activities!</center>
                </ItemPara>
            </Grid>

            {data.length !== 0 ?
                <Row>
                    {data.map((datas, index) => (
                        <Card
                            key={datas._id}
                            style={{
                                marginTop: "10px",
                                maxWidth: "100%",
                                backgroundColor: "cyan",
                            }}
                        >
                            <div style={{ padding: "0%" }}>
                                <h5 style={{ marginTop: "10px" }}>Admin Respond: {datas.date}</h5>
                                <CardContent>
                                    <ListItem style={{
                                        fontWeight: "600",
                                        margin: "0px"
                                    }}>
                                        Message: {datas.message ? datas.message : "-"}
                                    </ListItem>
                                </CardContent>
                            </div>
                            <YearlyBtn
                                size="small"
                                variant="outlined"
                                sx={{ marginLeft: "15px", backgroundColor: "rgb(19, 71, 129)", color: "white" }}
                                onClick={event => {
                                    setAttendance_date(datas.attendance_date)
                                    setRespondVisibility(true)
                                }} >
                                Respond
                            </YearlyBtn>

                            {respondVisibility &&
                                <form onSubmit={UserRespondFunction} >
                                    <StyTextFieldLeave
                                        style={{ backgroundColor: "#f5fafa", width: "100%" }}
                                        value={userMessage}
                                        label="Response to Activity."
                                        size="small"
                                        rows={3}
                                        variant="outlined"
                                        multiline
                                        placeholder="Response..."
                                        onChange={(e) => setUserMessage(e.target.value)}
                                        required
                                        fullWidth
                                    />
                                    <center>
                                        <div className="leave-btn">
                                            <YearlyBtn
                                                type="submit"
                                                sx={{
                                                    marginRight: "7px",
                                                    backgroundColor: "rgb(19, 71, 129)",
                                                    color: "white",
                                                    fontSize: "14px"
                                                }}
                                                variant="outlined"
                                            >
                                                Submit
                                            </YearlyBtn>
                                            <DltBtn
                                                variant="outlined"
                                                sx={{
                                                    backgroundColor: "rgb(197, 29, 29)",
                                                    color: "white",
                                                    fontSize: "14px"
                                                }}
                                                onClick={() => {
                                                    setRespondVisibility(false)
                                                }}
                                            >
                                                Cancel
                                            </DltBtn>
                                        </div>
                                    </center>
                                </form>}

                        </Card>
                    ))}
                </Row> : <center style={{ marginTop: "5px" }}><h4>No Data</h4></center>}
        </Box>
    );
}

export default EmployeeNotification;