import React, { useEffect, useState } from 'react'
import { Routes, Route, Link } from "react-router-dom";
import jwt_decode from 'jwt-decode';
import { Box, Drawer, Toolbar, IconButton, Menu } from "@mui/material";
import {
    Main,
    AppBar,
    DrawerHeader,
    MenuBtn,
} from "./css/styles";
import swal from "sweetalert";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import MenuIcon from "@material-ui/icons/Menu";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import Typography from "@mui/material/Typography";
import { useTheme } from "@mui/material/styles";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";

import Dashboard from './Pages/Dashboard'
import Attendance from './Pages/Attendance'
import Employees from './Pages/Employees'
import DrawerItems from "./Pages/DrawerItems";

import "./css/profile.css";
import ViewAttendance from './Pages/ViewAttendance';
import AdminRespondedActivity from './Pages/AdminRespondedActivity';
import EmploeeDashboard from './employee/EmploeeDashboard';
import EmploeeAttendance from './employee/EmploeeAttendance';
import EmploeeAttendanceHistory from './employee/EmploeeAttendanceHistory';
import EmployeeNotification from './employee/EmployeeNotification';


const drawerWidth = 230;
const transitionDuration = 350;

function Profile() {

    const _data = JSON.parse(localStorage.getItem('data'))
    const decoded_data = jwt_decode(_data);


    const [anchorElUser, setAnchorElUser] = useState(null);
    const theme = useTheme();



    const handleLogout = () => {
        swal("Logout of HRMS?", {
            buttons: ["Oh no!", true],
        }).then((value) => {
            if (value === true) {
                localStorage.removeItem("data");
                window.location.href = "/admin";
            }
        });
    };

    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget);
    };
    const handleCloseUserMenu = () => {
        setAnchorElUser(null);
    };

    // const classes = useStyles();
    const greaterThan375 = useMediaQuery("(min-width:375px)");
    const [open, setOpen] = React.useState(greaterThan375);

    useEffect(() => {
        setOpen(greaterThan375);
    }, [greaterThan375]);

    const handleMenuClick = () => {
        setOpen(!open);
    };

    function isHR() {
        const _data = JSON.parse(localStorage.getItem('data'))
        const decoded_data = jwt_decode(_data);

        return decoded_data.user.isHR === "true" ? true : false;
    }

    return (
        <div>
            <Box sx={{ display: "flex" }}>
                <AppBar
                    position="fixed"
                    open={open}
                    sx={{ backgroundColor: "rgb(19, 71, 129);" }} >
                    <Toolbar>
                        <IconButton
                            color="inherit"
                            onClick={handleMenuClick}
                            edge="start"
                            sx={{ mr: 2, ...(open && { display: "none" }) }}
                        >
                            <MenuIcon />
                        </IconButton>

                        <Box sx={{ flexGrow: 0, marginLeft: "auto" }}>
                            <Tooltip title="Open settings">
                                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                                    <Typography
                                        sx={{ color: "rgb(253, 253, 253)", marginRight: "8px" }}
                                    >
                                        {decoded_data.user.name}
                                    </Typography>
                                    <KeyboardArrowDownIcon sx={{ color: "white" }} />
                                </IconButton>
                            </Tooltip>
                            <Menu
                                sx={{ mt: "60px" }}
                                id="menu-appbar"
                                anchorEl={anchorElUser}
                                anchorOrigin={{
                                    vertical: "top",
                                    horizontal: "right",
                                }}
                                keepMounted
                                transformOrigin={{
                                    vertical: "top",
                                    horizontal: "right",
                                }}
                                open={Boolean(anchorElUser)}
                                onClose={handleCloseUserMenu}
                            >
                                <MenuItem onClick={handleCloseUserMenu}>
                                    <MenuBtn
                                        size="small"
                                        variant="text"
                                        color="error"
                                        onClick={handleLogout}
                                    >
                                        Logout
                                    </MenuBtn>
                                </MenuItem>
                            </Menu>
                        </Box>
                    </Toolbar>
                </AppBar>

                {/* DRAWER */}
                <Drawer
                    sx={{
                        width: drawerWidth,
                        flexShrink: 0,
                        "& .MuiDrawer-paper": {
                            width: drawerWidth,
                            boxSizing: "border-box",
                        },
                    }}
                    transitionDuration={{
                        enter: transitionDuration,
                        exit: transitionDuration,
                    }}
                    variant="persistent"
                    anchor="left"
                    open={open}
                >
                    <DrawerHeader
                        sx={{
                            minWidth: 0,
                            backgroundColor: "rgb(251,251,251)",
                            color: "rgb(19,71,129)",
                        }}
                    >
                        <strong
                            style={{
                                width: "100%",
                            }}
                        >
                            ASU
                        </strong>
                        <IconButton onClick={handleMenuClick}>
                            {theme.direction === "ltr" ? (
                                <ChevronLeftIcon
                                    sx={{
                                        color: "rgb(19,71,129)",
                                    }}
                                />
                            ) : (
                                <ChevronLeftIcon />
                            )}
                        </IconButton>
                    </DrawerHeader>
                    <div className="drawer-items">
                        {/* //Drawer Items */}

                        <DrawerItems open={open} isHR={isHR()} />
                    </div>
                </Drawer>

                {/* irems */}
                <Main open={open}>
                    <Routes>
                        <Route path="dashboard" element={<Dashboard />} />
                        <Route path="employees" element={<Employees />} />
                        <Route path="attendance" element={<Attendance />} />
                        <Route path="viewattendance" element={<ViewAttendance />} />
                        <Route path="adminresponses" element={<AdminRespondedActivity />} />

                        {/* USERS */}
                        {/* <Route path="employeedashboard" element={<EmploeeDashboard />} />
                        <Route path="employeeAttendance" element={<EmploeeAttendance />} />
                        <Route path="employeeAttendanceHistory" element={<EmploeeAttendanceHistory />} />
                        <Route path="employeeNotification" element={<EmployeeNotification />} /> */}
                    </Routes>
                </Main>
            </Box>
        </div>
    )
}

export default Profile