import React, { useState, useEffect } from "react";
import { Box, Typography, Paper, Grid } from "@mui/material";
import { styled } from "@mui/material/styles";
import activities from "../../images/activities.png";
import attendance from '../../images/attendance.png'
import "../css/profile.css";
import moment from 'moment';
import jwt_decode from 'jwt-decode';


const ItemPara = styled(Paper)(({ theme }) => ({
    padding: theme.spacing(1),
    textAlign: "left",
    "&:hover": {
        boxShadow: "3px 3px rgba(0,0,0,.1)",
    },
    color: "rgb(12, 0, 57)",
    borderRadius: "15px",
    boxShadow: "none",
}));

function Dashboard() {

    const [data, setData] = useState("")

    const _data = JSON.parse(localStorage.getItem('data'))
    const decoded_data = jwt_decode(_data);

    const getCounts = async () => {
        const date = moment(new Date()).format("YYYY-MM-DD")

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/user/get/dashboard/detail"
            : "/api/user/get/dashboard/detail";

        await fetch(URL, {
            method: 'POST',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                "date": date
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("Not 200")
                    } else {
                        const data = await response.json()
                        setData(data)
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    };

    // function redirectToActiveEmp() {
    //     window.location.href = './employees?status=active'
    // }

    useEffect(() => {
        getCounts()
    }, [])

    return (
        <div className="dash-main">
            <Box
                style={{ backgroundColor: "rgb(243, 243, 243)", height: "100%" }}
                component="main"
                sx={{ flexGrow: 1 }}
            >
                <Box sx={{ flexGrow: 1 }}>
                    <Grid container spacing={4} style={{ backgroundColor: "rgb(243, 243, 243)", paddingBottom: 20, paddingRight: 10 }}>
                        <Grid item xs={12} sm={6} md={4}>
                            {/* <ItemPara onClick={redirectToActiveEmp} sx={{ cursor: 'pointer' }}> */}
                            <ItemPara sx={{ cursor: 'pointer' }}>
                                <Typography>
                                    <h3 className="profile-h3">Employees</h3>
                                    <p className="profile-num">
                                        {data.user ? data.user : "NA"}
                                    </p>
                                    <img src={activities} className="img-profile" alt="" />
                                </Typography>
                            </ItemPara>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <ItemPara sx={{ cursor: 'pointer' }}>
                                <Typography>
                                    <h3 className="profile-h3">Attendance</h3>
                                    <p className="profile-num">
                                        {data.attendance ? data.attendance : "NA"}
                                    </p>
                                    <img src={attendance} className="img-profile" alt="" />
                                </Typography>
                            </ItemPara>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <ItemPara sx={{ cursor: 'pointer' }}>
                                <Typography>
                                    <h3 className="profile-h3">Activity</h3>
                                    <p className="profile-num">
                                        {data.activity ? data.activity : "NA"}
                                    </p>
                                    <img src={activities} className="img-profile" alt="" />
                                </Typography>
                            </ItemPara>
                        </Grid>
                    </Grid>
                </Box>
            </Box >
        </div >
    )
}

export default Dashboard