import React, { useState } from 'react';
import { authentication } from './config/firebase-config';
import { signInWithPhoneNumber, RecaptchaVerifier } from "firebase/auth";
import { DeviceUUID } from 'device-uuid';
import logo from '../../images/logo.png'

function EmployeeLogin(props) {

    const countryCode = "+91"
    const [expandForm, setExpandForm] = useState(false)
    const [phoneNumber, setPhoneNumber] = useState(countryCode)
    const [otp, setOTP] = useState("")
    const [email, setEmail] = useState("")
    const uuid = new DeviceUUID().parse();

    //generate recaptcha
    const generateRecaptcha = () => {
        window.recaptchaVerifier = new RecaptchaVerifier('recaptcha-container', {
            'size': 'invisible',
            'callback': (response) => {
            }
        }, authentication);
    }

    //check for phone number in backend after requesting for OTP
    const checkPhoneNumberBackend = async (device_id) => {
        const phone = phoneNumber.split('+91')[1]
        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/rol/otp/generate"
            : " /api/rol/otp/generate";

        await fetch(URL, {
            method: 'POST',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "phone": phone,
                "device_id": device_id
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log(response)
                    } else {
                        const result = await response.json()
                        if (result.error != "false") {
                            //if the device and phone number is not right for the user
                            if (result.status === 'NoDevice') {
                                alert(result.message)
                            } else {
                                setEmail(result.email)
                                // localStorage.setItem("data", JSON.stringify(data.data));
                            }
                        } else {
                            // Wrong phone Number
                            alert(result.message)
                        }

                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }

    //request for OTP
    const requestOTP = (e) => {
        e.preventDefault()

        if (uuid.platform === "Android" || uuid.platform === "iPhone") {


            //checking phone number in database
            const device_id = new DeviceUUID().get(); //get the uuid
            //call a fetch function after requesting for OTP
            checkPhoneNumberBackend(device_id)

            if (email != "") {
                if (phoneNumber.length >= 12) {
                    setExpandForm(true)
                    generateRecaptcha()
                    let appVerifier = window.recaptchaVerifier
                    signInWithPhoneNumber(authentication, phoneNumber, appVerifier)
                        .then(confirmationResult => {
                            window.confirmationResult = confirmationResult
                        }).catch((error) => {
                            console.log(error);
                        })
                }
                else alert("Please enter a valid phone number!")
            }
        }
        else alert("Please login in with a Phone!")
    }

    //user login final
    const userFinalLogin = async () => {
        const phone = phoneNumber.split('+91')[1]


        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/rol/otp/verify"
            : " /api/rol/otp/verify";

        await fetch(URL, {
            method: 'POST',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "phone": phone,
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        alert("Something went wrong!")
                    } else {
                        const result = await response.json()
                        localStorage.setItem("data", JSON.stringify(result));
                        window.location.href = "/employee/employee-dashboard"
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }


    //verify OTP
    const verifyOTP = async (e) => {
        // const otp = e.target.value
        // setOTP(otp)

        if (otp.length === 6) {
            const confirmationResult = window.confirmationResult
            await confirmationResult.confirm(otp).then((result) => {
                // User signed in successfully.
                const user = result.user;
                console.log(user);

                // valid phone number and also authenticated by google
                // login the user with token
                userFinalLogin()

            }).catch((error) => {
                alert("Something went wrong, try again!")
            });
        } else alert("Please enter the correct OTP!")
    }

    return (
        <div>

            <div className="Auth-form-container">
                <div className="Auth-form-content">
                    <center><img src={logo} height="200" width="200" /></center>
                    <h3 className="Auth-form-title" style={{ fontSize: 20, marginBottom: 5 }}>Login with your Phone Number</h3>
                    <div className="form-group mt-3">
                        <form onSubmit={requestOTP}>
                            <center>
                                <label style={{ float: 'left', marginBottom: 5 }}>Phone Number</label>
                                <input className="form-control mt-1" type='tel' id='phoneNumberInput' value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} />
                                {expandForm === true ?
                                    <>
                                        <label>Enter OTP</label>
                                        <p>OTP has been sent to your phone number.</p>
                                        <input type='Number' id='otpInput' value={otp} onChange={(e) => setOTP(e.target.value)} />
                                        <button type="submit" onClick={verifyOTP} className="btn btn-primary" style={{ fontSize: 12, marginLeft: 5 }}>Submit</button>
                                    </> : null}
                                {expandForm === false ? <button type="submit" className="btn btn-primary" style={{ marginTop: 10, }}>Request OTP</button> : null}
                            </center>
                            <div id='recaptcha-container'></div>
                        </form>
                    </div>
                </div>
            </div>




        </div>
    );
}

export default EmployeeLogin;