import React, { useState, useEffect } from "react";
import {
    Box,
    Paper,
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Modal,
} from "@mui/material";
import {
    ItemPara,
    StyTableCell,
    YearlyBtn,
    StyButtonDownload,
    LightTooltip,
    style,
    StyTextFieldLeave,
    DltBtn,
} from "../css/styles";
import Calendar from 'react-calendar';
import moment from 'moment';
// import '../css/calendar.css'
import swal from "sweetalert";
import 'react-calendar/dist/Calendar.css';
import "../css/profile.css"
import jwt_decode from 'jwt-decode';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

function Attendance() {
    const [date, setDate] = useState(new Date())
    const [empData, setEmpData] = useState([])
    const [viewEmpID, setViewEmpID] = useState("")
    const [empId, setEmpId] = useState("")
    const [name, setName] = useState("")
    const [dateDisplay, setDateDisplay] = useState("")
    const [searchedVal, setSearchedVal] = useState("");

    // modals
    const [dailyAttendaceDetails, setDailyAttendaceDetails] = useState(false);
    const [adminResponseModal, setAdminRespondModal] = useState(false)

    const [attendanceData, setAttendanceData] = useState([])
    const [message, setMessage] = useState("");
    const [getStatus, setGetStatus] = useState("");

    const [visible, setVisible] = useState(false);

    const onCalendarClicked = async (e) => {
        const displayDate = moment(date).format('LL')
        setDateDisplay(displayDate)

        const _data = JSON.parse(localStorage.getItem('data'))
        const dd = moment(date).format("YYYY-MM-DD")

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/history/bydate"
            : "/api/attendance/history/bydate";

        await fetch(URL, {
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            method: 'POST',
            headers: {
                "Accept": 'application/json',
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                "date": dd,
                "tokens": _data
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const data = await response.json()
                        setEmpData(data.data);
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }

    //Download employees
    const [selectDownloadDate, setSelectedDownloadDate] = useState(new Date());
    const handleDateChange = (e) => {
        setSelectedDownloadDate(e.target.value);
    };

    async function handleGetDownloadMonth(e) {
        e.preventDefault();
        swal("Download File?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                try {
                    const _data = JSON.parse(localStorage.getItem('data'))
                    const dd = moment(selectDownloadDate).format("YYYY-MM-DD")

                    const URL = window.location.href.startsWith("http://localhost")
                        ? "http://localhost:5000/api/export/attendance/month"
                        : "/api/export/attendance/month";
                    let res = await fetch(URL, {
                        method: "POST",
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify({
                            "date": dd,
                            "tokens": _data
                        }),
                    });

                    let resjson = await res.json();

                    if (!resjson.error) {
                        window.open("http://" + resjson.data, "_blank");
                        swal("Downloaded");
                    } else {
                        swal("Failed");
                    }
                } catch (err) {
                    console.log(err);
                }
            }
        });
    }

    // Handle when first pressed yes
    async function attendanceYesHandle(e) {
        e.preventDefault();

        swal("Mark Present for today?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                try {
                    const _data = JSON.parse(localStorage.getItem('data'))
                    const dd = moment(date).format("YYYY-MM-DD")

                    const URL = window.location.href.startsWith("http://localhost")
                        ? "http://localhost:5000/api/attendance/status"
                        : "/api/attendance/status";
                    await fetch(URL, {
                        method: "POST",
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify({
                            "emp_id": viewEmpID,
                            "response_status": "0",
                            "attendance_date": dd,
                            "tokens": _data
                        }),
                    })
                        .then(async response => {
                            console.log(response);
                            try {
                                if (response.status !== 200) {
                                    console.log("err")
                                } else {
                                    swal("Successful!")
                                    setVisible(false)
                                    setDailyAttendaceDetails(false)
                                }
                            } catch (err) {
                                console.log(err);
                            };
                        })
                        .catch(err => {
                            console.log(err);
                        });
                } catch (err) {
                    console.log(err);
                }
            }
        });
    }
    //get status
    async function getStatusFunction(emp_id) {
        const _data = JSON.parse(localStorage.getItem('data'))
        const dd = moment(date).format("YYYY-MM-DD")

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/get/status/id"
            : "/api/attendance/get/status/id";
        await fetch(URL, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                "emp_id": emp_id,
                "attendance_date": dd,
                "tokens": _data
            }),
        })
            .then(async response => {
                console.log(response);
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const data = await response.json()
                        setGetStatus(data.data)
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }

    // display the attendance list
    const displayAttendance = async (id, date, name) => {
        setDailyAttendaceDetails(true)

        const _data = JSON.parse(localStorage.getItem('data'))
        const dd = moment(date).format("YYYY-MM-DD")

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/history/id/date"
            : " /api/attendance/history/id/date";
        await fetch(URL, {
            method: 'POST',
            cache: 'no-cache',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                "emp_id": id,
                "date": dd,
                "tokens": _data
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const data = await response.json()
                        setAttendanceData(data.data);
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }

    //handle the admin response to activity
    const _data = JSON.parse(localStorage.getItem('data'))
    const decoded_data = jwt_decode(_data);
    const handleResponseSubmit = async (e) => {
        e.preventDefault();

        console.log(empId);

        const dd = moment(date).format("YYYY-MM-DD")

        swal("Submit?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                const URL = window.location.href.startsWith("http://localhost")
                    ? "http://localhost:5000/api/attendance/verify/activity"
                    : "/api/attendance/verify/activity";
                await fetch(URL, {
                    method: 'POST',
                    cache: 'no-cache',
                    headers: {
                        "Accept": 'application/json',
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({
                        "emp_id": empId,
                        "admin_id": decoded_data.user.admin_id,
                        "message": message,
                        "response_status": "1",
                        "attendance_date": dd,
                        "tokens": _data
                    })
                })
                    .then(async response => {
                        try {
                            if (response.status !== 200) {
                                console.log("err")
                            } else {
                                const data = await response.json()
                                console.log(data);
                                swal("Successful!");
                                setVisible(false)
                                setDailyAttendaceDetails(false)
                            }
                        } catch (err) {
                            console.log(err);
                        };
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }

        })

    }

    useEffect(() => {
        onCalendarClicked()
    }, [])

    // State variable to keep track of all the expanded rows
    // By default, nothing expanded. Hence initialized with empty array.
    const [expandedRows, setExpandedRows] = useState([]);

    // State variable to keep track which row is currently expanded.
    const [expandState, setExpandState] = useState({});

    const handleEpandRow = (event, userId) => {
        const currentExpandedRows = expandedRows;
        const isRowExpanded = currentExpandedRows.includes(userId);

        let obj = {};
        isRowExpanded ? (obj[userId] = false) : (obj[userId] = true);
        setExpandState(obj);

        // If the row is expanded, we are here to hide it. Hence remove
        // it from the state variable. Otherwise add to it.
        const newExpandedRows = isRowExpanded ?
            currentExpandedRows.filter(id => id !== userId) :
            currentExpandedRows.concat(userId);

        setExpandedRows(newExpandedRows);
    }

    return (
        <div>
            {/* yes and no respond */}
            {/* <Modal open={adminResponseModal} onClose={() => setAdminRespondModal(false)}>
                <Box sx={style}>
                    <center>

                        <DltBtn
                            variant="outlined"
                            color="error"
                            style={{
                                marginRight: 20
                            }}
                            onClick={(e) => {
                                setVisible(false)
                                attendanceYesHandle(e)
                            }}
                        >
                            Yes
                        </DltBtn>
                        <DltBtn
                            variant="outlined"
                            color="error"
                            onClick={() => setVisible(true)}
                        >
                            No
                        </DltBtn>

                        {visible && <form onSubmit={handleResponseSubmit} >
                            <StyTextFieldLeave
                                value={message}
                                label="Response to Activity."
                                size="small"
                                rows={3}
                                variant="outlined"
                                multiline
                                placeholder="Response..."
                                onChange={(e) => setMessage(e.target.value)}
                                required
                                fullWidth
                            />
                            <div className="leave-btn">
                                <YearlyBtn
                                    type="submit"
                                    sx={{
                                        marginRight: "7px",
                                    }}
                                    variant="outlined"
                                >
                                    Submit
                                </YearlyBtn>
                                <DltBtn
                                    variant="outlined"
                                    color="error"
                                    onClick={() => {
                                        setVisible(false)
                                        setAdminRespondModal(false)
                                    }}
                                >
                                    Cancel
                                </DltBtn>
                            </div>
                        </form>}
                    </center>
                </Box>
            </Modal> */}


            {/* Attendance Admin response */}
            <Modal
                style={{
                    display: "flex",
                    justifyContent: "center",
                }}
                open={dailyAttendaceDetails} onClose={() => setDailyAttendaceDetails(false)}>
                <TableContainer sx={{ boxShadow: "none", width: '80%', alignSelf: 'center' }} component={Paper}>
                    <Table
                        sx={{
                            minWidth: 300,
                            borderBottom: "3px solid rgb(19, 71, 129)",
                        }}
                        aria-label="simple table"
                    >
                        <TableHead>
                            <TableRow
                                sx={{
                                    backgroundColor: "rgb(19, 71, 129)",
                                }}
                            >
                                <StyTableCell>Sign In time</StyTableCell>
                                <StyTableCell>Sign Out Time</StyTableCell>
                                <StyTableCell>Distance In (mtrs)</StyTableCell>
                                <StyTableCell>Distance Out (mtrs)</StyTableCell>
                                <StyTableCell>Time of Work</StyTableCell>
                                <StyTableCell>Activity</StyTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {attendanceData.map((attendance, index) => (
                                <TableRow className="expandableTableValue"
                                    key={index}
                                    sx={{
                                        "&:last-child td, &:last-child th": {
                                            border: 0,
                                        },
                                    }}
                                >
                                    <TableCell align="center">
                                        {attendance.in_time ? attendance.in_time : "-"}
                                    </TableCell>
                                    <TableCell align="center">
                                        {attendance.out_time ? attendance.out_time : "-"}
                                    </TableCell>
                                    <TableCell align="center">
                                        {attendance.distance_in ? attendance.distance_in : "-"}
                                    </TableCell>
                                    <TableCell align="center">
                                        {attendance.distance_out ? attendance.distance_out : "-"}
                                    </TableCell>
                                    <TableCell align="center">
                                        {attendance.total_time ? attendance.total_time : "-"}
                                    </TableCell>
                                    <TableCell align="center">
                                        {attendance.activity ? attendance.activity : "-"}
                                    </TableCell>
                                </TableRow>
                            ))}

                        </TableBody>

                    </Table>

                </TableContainer>


            </Modal>


            <div style={{ marginTop: "7%" }}>

                <Grid item xs={12} sm={6} md={9}>
                    <div style={{ marginLeft: "15px" }}>
                        <DatePicker
                            selected={selectDownloadDate}
                            dateFormat="MMMM yyyy"
                            showMonthYearPicker
                            maxDate={new Date()}
                            onChange={(e) => {
                                setSelectedDownloadDate(e);
                            }}
                        /> <span>Select the date</span>
                    </div>
                    {/* <LightTooltip title="Select a Month" placement="right">
                        <TextField
                            size="small"
                            type="month"
                            sx={{ marginLeft: "2%" }}
                            inputProps={{
                                // max: getformattedDate(),
                            }}
                            variant="outlined"
                            value={selectDownloadDate}
                            onChange={(e) => {
                                handleDateChange(e);
                            }}
                            required
                        />
                    </LightTooltip> */}
                </Grid>
                <StyButtonDownload
                    variant="contained"
                    sx={{ marginLeft: "2%" }}
                    onClick={handleGetDownloadMonth}
                >
                    Download By Month
                </StyButtonDownload>


                {/* Calendar */}
                <div>
                    <center>
                        <h5 className="header">Select the Date</h5>
                        <div className="calendar-container">
                            <Calendar onChange={setDate}
                                value={date}
                                onClickDay={onCalendarClicked}
                                maxDate={new Date()}
                            />
                        </div>
                        <p className="header">Double Click the Date</p>
                    </center>
                </div>

                {/* list */}
                <Box sx={{ flexGrow: 1 }} style={{ marginTop: "5%" }}>
                    <Grid container spacing={0}>
                        <Grid item xs={12} sm={12} md={9}>
                            <ItemPara>
                                <span style={{ fontWeight: "600" }}>Employee Attendance on {dateDisplay}</span>
                            </ItemPara>
                        </Grid>
                        <Grid item xs={12} sm={12} md={3}>
                            <ItemPara>
                                <TextField
                                    sx={{ float: "right" }}
                                    size="small"
                                    variant="outlined"
                                    placeholder="Name..."
                                    label="Search Employee..."
                                    onChange={(e) => setSearchedVal(e.target.value)}
                                />
                            </ItemPara>
                        </Grid>


                        <Grid item xs={12} md={12}>
                            <ItemPara>
                                <TableContainer sx={{ boxShadow: "none" }} component={Paper}>
                                    <Table
                                        sx={{
                                            minWidth: 300,
                                            borderBottom: "3px solid rgb(19, 71, 129)",
                                        }}
                                        aria-label="simple table"
                                    >
                                        <TableHead>
                                            <TableRow
                                                sx={{
                                                    backgroundColor: "rgb(19, 71, 129)",
                                                }}
                                            >
                                                <StyTableCell>User</StyTableCell>
                                                <StyTableCell>Phone</StyTableCell>
                                                <StyTableCell>Total Time</StyTableCell>
                                                <StyTableCell>Status</StyTableCell>
                                                <StyTableCell>Details</StyTableCell>
                                                <StyTableCell>Action</StyTableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {empData
                                                .filter
                                                ((row) =>
                                                    // note that I've incorporated the searchedVal length check here
                                                    !searchedVal.length || row.name
                                                        .toString()
                                                        .toLowerCase()
                                                        .includes(searchedVal.toString().toLowerCase())
                                                )
                                                .map((emp, index) =>
                                                    <TableRow
                                                        key={index}
                                                        sx={{
                                                            "&:last-child td, &:last-child th": {
                                                                border: 0,
                                                            },
                                                        }}
                                                    >
                                                        <TableCell align="center">
                                                            {emp.empID.name}
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            {emp.empID.phone ? emp.empID.phone : "-"}
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            {emp.totalhr + "hrs " + emp.totalMn + "mins"}
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            {emp.attendanceStatus === null ? "Not Marked" : (emp.attendanceStatus.response_status === 0 ? "Present" : "Absent")}
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            <YearlyBtn
                                                                size="small"
                                                                variant="outlined"
                                                                sx={{ marginRight: "15px" }}
                                                                onClick={event => {
                                                                    setViewEmpID(emp.emp_id)
                                                                    setEmpId(emp._id)
                                                                    setName(emp.name)
                                                                    displayAttendance(emp.emp_id, date, emp.name)
                                                                    getStatusFunction(emp.emp_id)

                                                                }} >
                                                                View
                                                            </YearlyBtn>
                                                        </TableCell>
                                                        <TableCell align="center">

                                                            {(emp.attendanceStatus === null || emp.attendanceStatus.is_admin_responded === 0) ?
                                                                <div>
                                                                    <YearlyBtn
                                                                        style={{ fontSize: "11px" }}
                                                                        size="small"
                                                                        variant="outlined"
                                                                        sx={{ marginRight: "15px" }}
                                                                        onClick={e => {
                                                                            setVisible(false)
                                                                            attendanceYesHandle(e)
                                                                        }} >Mark as Present</YearlyBtn>
                                                                    <YearlyBtn
                                                                        style={{ fontSize: "11px" }}
                                                                        size="small"
                                                                        variant="outlined"
                                                                        sx={{ marginRight: "15px" }}
                                                                        onClick={e => {
                                                                            setEmpId(emp.empID._id)
                                                                            setVisible(true)

                                                                        }} >Give Explanation</YearlyBtn>
                                                                </div> : <div>Responded</div>}

                                                            {visible && <form onSubmit={handleResponseSubmit} >
                                                                <StyTextFieldLeave
                                                                    value={message}
                                                                    label="Response to Activity."
                                                                    size="small"
                                                                    rows={3}
                                                                    variant="outlined"
                                                                    multiline
                                                                    placeholder="Response..."
                                                                    onChange={(e) => setMessage(e.target.value)}
                                                                    required
                                                                    fullWidth
                                                                />
                                                                <div className="leave-btn">
                                                                    <YearlyBtn
                                                                        type="submit"
                                                                        sx={{
                                                                            marginRight: "7px",
                                                                        }}
                                                                        variant="outlined"
                                                                    >
                                                                        Submit
                                                                    </YearlyBtn>
                                                                    <DltBtn
                                                                        variant="outlined"
                                                                        color="error"
                                                                        onClick={() => {
                                                                            setVisible(false)
                                                                        }}
                                                                    >
                                                                        Cancel
                                                                    </DltBtn>
                                                                </div>
                                                            </form>}

                                                        </TableCell>

                                                    </TableRow>
                                                )}

                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </ItemPara>
                        </Grid>
                    </Grid>
                </Box >
            </div >
        </div >
    )
}

export default Attendance