import React, { useState, useEffect } from "react";
import {
    Box,
    Paper,
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
} from "@mui/material";
import {
    ItemPara,
    StyButton,
    StyTableCell,
} from "../css/styles";

function Employees() {

    const [empData, setEmpData] = useState([])
    const [searchedVal, setSearchedVal] = useState("");

    // Pagination
    const [pages, setPage] = useState({
        current: 1,
        next: 1,
        total: 1,
    });
    const [numberOfItems, setNumberOfItems] = useState();
    function prePage() {
        if (pages.current > 1) {
            pages.next = pages.current - 1;
            getEmployees();
        }
    }
    function nextPage() {
        if (pages.current < pages.total) {
            pages.next = pages.current + 1;
            getEmployees();
        }
    }


    // get Users Init
    const getEmployees = async () => {
        //get the data
        const _data = await JSON.parse(localStorage.getItem('data'))
        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/user/get/all"
            : " /api/user/get/all";

        await fetch(URL, {
            method: 'POST',
            cache: 'no-cache',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                "tokens": _data,
                current_page: pages.next
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("Not 200")
                    } else {
                        const data = await response.json()
                        setEmpData(data.data);
                        setNumberOfItems(data.total_items);
                        setPage({
                            current: data.current_page,
                            total: data.total_page,
                            next: data.current_page,
                        });
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    };

    useEffect(() => {
        getEmployees()
    }, [])





    return (
        <>
            <Box sx={{ flexGrow: 1 }} style={{ marginTop: "7%" }}>
                <Grid container spacing={0}>
                    <Grid item xs={12} sm={12} md={9}>
                        <ItemPara>
                            Employee List
                        </ItemPara>
                    </Grid>
                    <Grid item xs={12} sm={12} md={3}>
                        <ItemPara>
                            <TextField
                                sx={{ float: "right" }}
                                size="small"
                                variant="outlined"
                                placeholder="Name..."
                                label="Search Employee..."
                                onChange={(e) => setSearchedVal(e.target.value)}
                            />
                        </ItemPara>
                    </Grid>

                    {/* Employee details table */}
                    <Grid item xs={12} md={12}>
                        <ItemPara>
                            <TableContainer sx={{ boxShadow: "none" }} component={Paper}>
                                <Table
                                    sx={{
                                        minWidth: 300,
                                        borderBottom: "3px solid rgb(19, 71, 129)",
                                    }}
                                    aria-label="simple table"
                                >
                                    <TableHead>
                                        <TableRow
                                            sx={{
                                                backgroundColor: "rgb(19, 71, 129)",
                                            }}
                                        >
                                            <StyTableCell>Name</StyTableCell>
                                            <StyTableCell>Email</StyTableCell>
                                            <StyTableCell>Designation</StyTableCell>
                                            <StyTableCell>Phone</StyTableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {empData
                                            .filter((row) =>
                                                // note that I've incorporated the searchedVal length check here
                                                !searchedVal.length || row.name
                                                    .toString()
                                                    .toLowerCase()
                                                    .includes(searchedVal.toString().toLowerCase())
                                            )
                                            .map((emp, index) => (
                                                <TableRow
                                                    key={index}
                                                    sx={{
                                                        "&:last-child td, &:last-child th": {
                                                            border: 0,
                                                        },
                                                    }}
                                                >
                                                    <TableCell align="center">
                                                        {emp.name}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {emp.email ? emp.email : "-"}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {emp.designation ? emp.designation : "-"}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {emp.phone ? emp.phone : "-"}
                                                    </TableCell>
                                                </TableRow>
                                            ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>

                        </ItemPara>
                    </Grid>
                </Grid>

                {/* Pagination Buttons */}

                <Grid container direction="column" alignItems="center" justify="center">
                    <Grid item xs={3}>
                        <StyButton
                            variant="outlined"
                            sx={{ marginRight: "15px" }}
                            onClick={prePage}
                        >
                            Pre
                        </StyButton>
                        <StyButton variant="outlined"
                            onClick={nextPage}
                        >
                            Next
                        </StyButton>{" "}
                        <br />
                    </Grid>
                </Grid>
            </Box >
        </>
    )
}

export default Employees