import React, { useState, useEffect } from "react";
import { Box, Typography, Paper, Grid } from "@mui/material";
import { styled } from "@mui/material/styles";
import activities from "../../images/activities.png";
import attendance from '../../images/attendance.png'
import "../css/profile.css";
import moment from 'moment';
import jwt_decode from 'jwt-decode';

const ItemPara = styled(Paper)(({ theme }) => ({
    padding: theme.spacing(1),
    textAlign: "left",
    "&:hover": {
        boxShadow: "3px 3px rgba(0,0,0,.1)",
    },
    color: "rgb(12, 0, 57)",
    borderRadius: "15px",
    boxShadow: "none",
}));

function EmploeeDashboard(props) {
    const [dateDisplay, setDateDisplay] = useState("")

    useEffect(() => {
        const displayDate = moment(new Date()).format('LL')
        setDateDisplay(displayDate)
    }, [])

    function redirectToEmpAttendance() {
        window.location.href = '/employee/employee-attendance'
    }
    function redirectToEmpHistory() {
        window.location.href = '/employee/employee-attendance-history'
    }
    function redirectToEmpResponses() {
        window.location.href = '/employee/employee-notification'
    }
    return (
        <div className="dash-main">
            <Box
                style={{ backgroundColor: "rgb(243, 243, 243)", height: "100%" }}
                component="main"
                sx={{ flexGrow: 1 }}
            >
                <Grid style={{ backgroundColor: "white", marginTop: 20, marginLeft: 0 }}>
                    <ItemPara>
                        <span style={{ fontWeight: "600" }}>Today: </span> {dateDisplay}
                    </ItemPara>
                </Grid>

                <Box sx={{ flexGrow: 1 }}>
                    <Grid container spacing={0} style={{ backgroundColor: "white", paddingBottom: 20, paddingRight: 10 }}>
                        <Grid>
                            <ItemPara onClick={redirectToEmpAttendance} sx={{ cursor: 'pointer' }} >
                                <ItemPara sx={{ cursor: 'pointer' }} style={{ backgroundColor: "cyan" }}>
                                    <Typography>
                                        <h3 className="profile-h3">Attendance</h3>
                                        <div className="profile-num">
                                            {/* {data.user ? data.user : "NA"} */}
                                        </div>
                                        <img src={activities} className="img-profile" alt="" />
                                    </Typography>
                                </ItemPara>
                            </ItemPara>
                        </Grid>
                        <Grid >
                            <ItemPara onClick={redirectToEmpHistory} sx={{ cursor: 'pointer' }}>
                                <ItemPara sx={{ cursor: 'pointer' }} style={{ backgroundColor: "cyan" }}>
                                    <Typography>
                                        <h3 className="profile-h3">History</h3>
                                        <div className="profile-num">
                                        </div>
                                        <img src={attendance} className="img-profile" alt="" />
                                    </Typography>
                                </ItemPara>
                            </ItemPara>
                        </Grid>
                        <Grid>
                            <ItemPara onClick={redirectToEmpResponses} sx={{ cursor: 'pointer' }}>
                                <ItemPara sx={{ cursor: 'pointer' }} style={{ backgroundColor: "cyan" }}>
                                    <Typography>
                                        <h3 className="profile-h3">Responses</h3>
                                        <div className="profile-num">
                                        </div>
                                        <img src={activities} className="img-profile" alt="" />
                                    </Typography>
                                </ItemPara>
                            </ItemPara>
                        </Grid>
                    </Grid>
                </Box>
            </Box >
        </div >
    );
}

export default EmploeeDashboard;