import React, { useEffect } from 'react'
import { Link } from "react-router-dom";
import {
    Divider,
    List,
    ListItemIcon,
    ListItemText,
    CardHeader,
} from "@mui/material";
import "../css/profile.css";
import { LightTooltip } from "../css/styles";
import ListItemButton from "@mui/material/ListItemButton";
import DashboardCustomizeIcon from "@mui/icons-material/DashboardCustomize";
import ReceiptLongIcon from "@mui/icons-material/ReceiptLong";
import VaccinesIcon from "@mui/icons-material/Vaccines";
import ReceiptIcon from "@mui/icons-material/Receipt";
import { useLocation } from "react-router-dom";

function DrawerItems({ isHR, open }) {
    const [link, setLink] = React.useState("dashboard");
    const location = useLocation();

    useEffect(() => {
        const path = window.location.pathname.split("/");
        setLink(path[path.length - 1]);
    }, [location]);

    return (
        <List className="list-main">
            <div className="list-d">
                <div>
                    <Link style={{ textDecoration: 'none' }} to="dashboard"  >
                        <LightTooltip title="Dashboard" placement="right">
                            <ListItemButton
                                sx={{
                                    minHeight: 48,
                                    justifyContent: open ? "initial" : "center",
                                    px: 2.5,
                                    backgroundColor:
                                        link === "dashboard"
                                            ? "rgb(234, 165, 27)"
                                            : "transparent",
                                }}
                            >
                                <ListItemIcon
                                    sx={{
                                        color: "rgb(250, 250, 250)",
                                        minWidth: 0,
                                        mr: open ? 3 : "auto",
                                        justifyContent: "center",
                                    }}
                                >
                                    <DashboardCustomizeIcon />
                                </ListItemIcon>
                                <ListItemText
                                    sx={{
                                        opacity: open ? 1 : 0,
                                        color: "rgb(250, 250, 250)",
                                    }}
                                >
                                    Dashboard
                                </ListItemText>
                            </ListItemButton>
                        </LightTooltip>
                    </Link>

                    <Link style={{ textDecoration: 'none' }} to="employees" >
                        <LightTooltip title="Employees" placement="right">
                            <ListItemButton
                                sx={{
                                    minHeight: 48,
                                    justifyContent: open ? "initial" : "center",
                                    px: 2.5,
                                    backgroundColor:
                                        link === "employees"
                                            ? "rgb(234, 165, 27)"
                                            : "transparent",
                                }}
                            >
                                <ListItemIcon
                                    sx={{
                                        color: "rgb(250, 250, 250)",
                                        minWidth: 0,
                                        mr: open ? 3 : "auto",
                                        justifyContent: "center",
                                    }}
                                >
                                    <ReceiptLongIcon />
                                </ListItemIcon>
                                <ListItemText
                                    sx={{
                                        opacity: open ? 1 : 0,
                                        color: "rgb(250, 250, 250)",
                                    }}
                                >
                                    Employees
                                </ListItemText>
                            </ListItemButton>
                        </LightTooltip>
                    </Link>

                    <Link style={{ textDecoration: 'none' }} to="attendance" >
                        <LightTooltip title="Attendance" placement="right">
                            <ListItemButton
                                sx={{
                                    minHeight: 48,
                                    justifyContent: open ? "initial" : "center",
                                    px: 2.5,
                                    backgroundColor:
                                        link === "attendance" ? "rgb(234, 165, 27)" : "transparent",
                                }}
                            >
                                <ListItemIcon
                                    sx={{
                                        color: "rgb(250, 250, 250)",
                                        minWidth: 0,
                                        mr: open ? 3 : "auto",
                                        justifyContent: "center",
                                    }}
                                >
                                    <ReceiptIcon />
                                </ListItemIcon>
                                <ListItemText
                                    sx={{
                                        opacity: open ? 1 : 0,
                                        color: "rgb(250, 250, 250)",
                                    }}
                                >
                                    Attendance
                                </ListItemText>
                            </ListItemButton>
                        </LightTooltip>
                    </Link>

                    <Link style={{ textDecoration: 'none' }} to="adminresponses" >
                        <LightTooltip title="Responses" placement="right">
                            <ListItemButton
                                sx={{
                                    minHeight: 48,
                                    justifyContent: open ? "initial" : "center",
                                    px: 2.5,
                                    backgroundColor:
                                        link === "adminresponses" ? "rgb(234, 165, 27)" : "transparent",
                                }}
                            >
                                <ListItemIcon
                                    sx={{
                                        color: "rgb(250, 250, 250)",
                                        minWidth: 0,
                                        mr: open ? 3 : "auto",
                                        justifyContent: "center",
                                    }}
                                >
                                    <ReceiptIcon />
                                </ListItemIcon>
                                <ListItemText
                                    sx={{
                                        opacity: open ? 1 : 0,
                                        color: "rgb(250, 250, 250)",
                                    }}
                                >
                                    Activity Responses
                                </ListItemText>
                            </ListItemButton>
                        </LightTooltip>
                    </Link>

                </div>
            </div>
        </List>
    )
}

export default DrawerItems