import './App.css';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css"
import Login from './componenets/Login';
import Error404 from './componenets/Error404';
import Profile from './componenets/Profile';
import EmployeeLogin from './componenets/employee/EmployeeLogin';
import jwt_decode from 'jwt-decode';
import EmployeeProfile from './componenets/employee/EmployeeProfile';


function App() {
  const logCheck = localStorage.getItem("data");
  let decoded_data = ""
  if (logCheck != null) {
    decoded_data = jwt_decode(logCheck)
  }

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<EmployeeLogin />} />
        <Route path="/admin" element={<Login />} />

        {logCheck ?
          (decoded_data.user.isUser === true ? <Route path="/employee/*" element={<EmployeeProfile />} />
            : <Route path="/profile/*" element={<Profile />} />)
          : (
            <Route path="/" element={<App />} />
          )}

        {/* route for random/ wrong URLs */}
        <Route path="*" element={<Error404 />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
