import React, { useState } from "react";
import swal from "sweetalert";

function Login() {

    const [email, setEmail] = useState();
    const [password, setPassword] = useState();

    const handleSubmit = async (e) => {
        e.preventDefault();

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/rol/admin/login"
            : " /api/rol/admin/login";

        await fetch(URL, {
            method: 'post',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "email": email,
                "password": password
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const data = await response.json()
                        if (data.data == null) {
                            swal("Login Failed", data.message);
                        } else {
                            swal("Logged In successfully", {
                                buttons: false,
                                timer: 2000,
                            }).then((value) => {
                                localStorage.setItem("data", JSON.stringify(data.data));
                                window.location.href = "/profile/dashboard";
                            });

                        }
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });

        // const response = await loginUser({
        //     email,
        //     password,
        // });

        /* check if the data accessing has passed/ failed */
        // console.log(response)

        // if (response.json() !== 200) {
        //     swal("Logged In success", {
        //         buttons: false,
        //         timer: 2000,
        //     }).then((value) => {
        //         // localStorage.setItem("data", JSON.stringify(response["data"]));
        //         // window.location.href = "/profile/dashboard";
        //         console.log("Success");
        //     });
        // } else {
        //     swal("Login Failed");
        // }
    };

    return (
        <div className="Auth-form-container">
            <form className="Auth-form" onSubmit={handleSubmit} method="POST">
                <div className="Auth-form-content">
                    <h3 className="Auth-form-title">Sign In</h3>
                    <div className="form-group mt-3">
                        <label>Email</label>
                        <input
                            onChange={(e) => setEmail(e.target.value.toLowerCase())}
                            type="email"
                            name="email"
                            id="email"
                            className="form-control mt-1"
                            placeholder="Enter email"
                            required
                        />
                    </div>
                    <div className="form-group mt-3">
                        <label>Password</label>
                        <input
                            onChange={(e) => setPassword(e.target.value)}
                            type="password"
                            name="password"
                            id="password"
                            className="form-control mt-1"
                            placeholder="Enter password"
                            required
                        />
                    </div>
                    <div className="d-grid gap-2 mt-3">
                        <button type="submit" className="btn btn-primary">
                            Submit
                        </button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default Login