import React, { useState, useEffect } from "react";
import {
    Box,
    Paper,
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Modal,
} from "@mui/material";
import {
    style,
    ItemPara,
    YearlyBtn,
    StyTableCell,
    DltBtn,
} from "../css/styles";
import jwt_decode from 'jwt-decode';
import { BASE_URL } from "../config";
import swal from "sweetalert";


function AdminRespondedActivity(props) {
    const [respondedData, setRespondeddata] = useState([])
    const [searchedVal, setSearchedVal] = useState("");

    const [empID, setEmpID] = useState("");
    const [attendanceDate, setAttendanceDate] = useState("");
    const _data = JSON.parse(localStorage.getItem('data'))


    const getRespondedData = async () => {
        // e.preventDefault()
        //get the data
        const data_decoded = jwt_decode(_data)

        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/get/verify/activity"
            : "/api/attendance/get/verify/activity";
        await fetch(URL, {
            method: 'POST',
            cache: 'no-cache',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                "admin_id": data_decoded.user.admin_id,
                "tokens": _data
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("Not 200")
                    } else {
                        const data = await response.json()
                        setRespondeddata(data.data);
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    };


    // IF YES
    const changeToYes = async (e) => {
        e.preventDefault();

        swal("Are you Sure?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                try {
                    const URL = window.location.href.startsWith("http://localhost")
                        ? "http://localhost:5000/api/attendance/status/update"
                        : "/api/attendance/status/update";
                    await fetch(URL, {
                        method: 'POST',
                        cache: 'no-cache',
                        headers: {
                            "Accept": 'application/json',
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify({
                            "emp_id": empID,
                            "attendance_date": attendanceDate,
                            "response_status": 0,
                            "tokens": _data
                        })
                    })
                        .then(async response => {
                            try {
                                if (response.status !== 200) {
                                    console.log("Not 200")
                                } else {
                                    const data = await response.json()
                                    getRespondedData()
                                }
                            } catch (err) {
                                console.log(err);
                            };
                        })
                        .catch(err => {
                            console.log(err);
                        });
                } catch (err) {
                    console.log(err);
                }
            }
        });
    }

    // IF NO
    const changeToNo = async (e) => {

        e.preventDefault();

        swal("Are you Sure?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                try {

                    const URL = window.location.href.startsWith("http://localhost")
                        ? "http://localhost:5000/api/attendance/status/update"
                        : "/api/attendance/status/update";
                    await fetch(URL, {
                        method: 'POST',
                        cache: 'no-cache',
                        headers: {
                            "Accept": 'application/json',
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify({
                            "emp_id": empID,
                            "attendance_date": attendanceDate,
                            "response_status": 1,
                            "tokens": _data
                        })
                    })
                        .then(async response => {
                            try {
                                if (response.status !== 200) {
                                    console.log("Not 200")
                                } else {
                                    const data = await response.json()
                                    getRespondedData()
                                }
                            } catch (err) {
                                console.log(err);
                            };
                        })
                        .catch(err => {
                            console.log(err);
                        });
                } catch (err) {
                    console.log(err);
                }


            }
        });
    }


    useEffect(() => {
        getRespondedData()
    }, [])

    return (
        <>
            {/* yes and no respond */}
            {/* <div>
                <Modal open={visible} onClose={() => setVisible(false)}>
                    <Box sx={style}>
                        <center>

                            <DltBtn
                                variant="outlined"
                                color="error"
                                style={{
                                    marginRight: 20
                                }}
                                onClick={(e) => {
                                    changeToYes(e)
                                    setVisible(false)
                                }}
                            >
                                Present
                            </DltBtn>
                            <DltBtn
                                variant="outlined"
                                color="error"
                                onClick={(e) => {
                                    changeToNo(e)
                                    setVisible(false)
                                }}
                            >
                                Absent
                            </DltBtn>
                        </center>
                    </Box>
                </Modal>
            </div> */}

            {/* Show activity */}
            {/* <div>
                <Modal open={responseModal} onClose={() => setResponseModal(false)}>
                    <Box sx={style}>
                        <div className="leave-reason"><span className="sub-mes">Message:</span> {message}</div>
                    </Box>
                </Modal>
            </div> */}


            <Box sx={{ flexGrow: 1 }} style={{ marginTop: "7%" }}>
                <Grid container spacing={0}>
                    <Grid item xs={12} sm={12} md={9}>
                        <ItemPara>
                            Responded list
                        </ItemPara>
                    </Grid>
                    <Grid item xs={12} sm={12} md={3}>
                        <ItemPara>
                            <TextField
                                sx={{ float: "right" }}
                                size="small"
                                variant="outlined"
                                placeholder="Name..."
                                label="Search Employee..."
                                onChange={(e) => setSearchedVal(e.target.value)}
                            />
                        </ItemPara>
                    </Grid>

                    {/* Employee details table */}
                    <Grid item xs={12} md={12}>
                        <ItemPara>
                            <TableContainer sx={{ boxShadow: "none" }} component={Paper}>
                                <Table
                                    sx={{
                                        minWidth: 300,
                                        borderBottom: "3px solid rgb(19, 71, 129)",
                                    }}
                                    aria-label="simple table"
                                >
                                    <TableHead>
                                        <TableRow
                                            sx={{
                                                backgroundColor: "rgb(19, 71, 129)",
                                            }}
                                        >
                                            <StyTableCell>Name</StyTableCell>
                                            <StyTableCell>Date</StyTableCell>
                                            <StyTableCell>Admin Response</StyTableCell>
                                            <StyTableCell>User Response</StyTableCell>
                                            <StyTableCell>Final Attendance</StyTableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {respondedData
                                            .filter((row) =>
                                                // note that I've incorporated the searchedVal length check here
                                                !searchedVal.length || row.emp_id.name
                                                    .toString()
                                                    .toLowerCase()
                                                    .includes(searchedVal.toString().toLowerCase())
                                            )
                                            .map((emp, index) => (
                                                <TableRow
                                                    key={index}
                                                    sx={{
                                                        "&:last-child td, &:last-child th": {
                                                            border: 0,
                                                        },
                                                    }}
                                                >
                                                    <TableCell align="center">
                                                        {emp.emp_id.name ? emp.emp_id.name : "-"}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {emp.date ? emp.date : "-"}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {emp.message}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {/* {emp.status === 1 ? "Confirmed By Employee" : (emp.emp_response === null ? '-' : emp.response)} */}
                                                        {emp.emp_response !== null ? emp.emp_response : "-"}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        {emp.final_verify_status === 0 ? <div>
                                                            <YearlyBtn
                                                                style={{ fontSize: "11px" }}
                                                                size="small"
                                                                variant="outlined"
                                                                sx={{ marginRight: "15px" }}
                                                                onClick={e => {
                                                                    setEmpID(emp.emp_id.emp_id)
                                                                    setAttendanceDate(emp.attendance_date)
                                                                    changeToYes(e)
                                                                }} >Present</YearlyBtn>
                                                            <YearlyBtn
                                                                style={{ fontSize: "11px" }}
                                                                size="small"
                                                                variant="outlined"
                                                                sx={{ marginRight: "15px" }}
                                                                onClick={e => {
                                                                    setEmpID(emp.emp_id.emp_id)
                                                                    setAttendanceDate(emp.attendance_date)
                                                                    changeToNo(e)
                                                                }} >Absent</YearlyBtn>
                                                        </div> : <div>Responded</div>}
                                                    </TableCell>
                                                </TableRow>
                                            ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                            {/* <span className="total" style={{ float: "right" }}>
                                Total Entries: {numberOfItems}
                            </span> */}
                        </ItemPara>
                    </Grid>
                </Grid>

                {/* Pagination Buttons */}

                {/* <Grid container direction="column" alignItems="center" justify="center">
                    <Grid item xs={3}>
                        <StyButton
                            variant="outlined"
                            sx={{ marginRight: "15px" }}
                        // onClick={prePage}
                        >
                            Pre
                        </StyButton>
                        <StyButton variant="outlined"
                        // onClick={nextPage}
                        >
                            Next
                        </StyButton>{" "}
                        <br />
                    </Grid>
                </Grid> */}
            </Box >
        </>
    );
}

export default AdminRespondedActivity;