import React, { useEffect, useState } from 'react';
import {
    Box,
    Modal,
    TableCell,
    TableRow,
} from "@mui/material";
import {
    style,
    ItemPara,
    StyTableCell,
    StyTextFieldLeave,
    YearlyBtn,
    DltBtn,
} from "../css/styles"
import "../css/profile.css";
import moment from 'moment';
import swal from "sweetalert";
import jwt_decode from 'jwt-decode';
import { BASE_URL } from '../config';

function ViewAttendance({ id, date, name }) {
    // Modals
    const [adminResponseModal, setAdminRespondModal] = useState(false)
    const [activityModal, setActivityModal] = useState(false)

    // Value states
    const [attendanceData, setAttendanceData] = useState([])
    const [activity, setActivity] = useState("");
    const [subject, setSubject] = useState("");
    const [message, setMessage] = useState("");
    const [attendanceId, setAttendanceId] = useState("");

    const _data = JSON.parse(localStorage.getItem('data'))
    const decoded_data = jwt_decode(_data);

    //handle the admin response to activity
    const handleResponseSubmit = async (e) => {
        e.preventDefault();

        swal("Submit?", {
            buttons: ["Oh no!", true],
        }).then(async (value) => {
            if (value === true) {
                const URL = window.location.href.startsWith("http://localhost")
                    ? "http://localhost:5000/api/attendance/verify/activity"
                    : "/api/attendance/verify/activity";
                await fetch(URL, {
                    method: 'POST',
                    cache: 'no-cache',
                    headers: {
                        "Accept": 'application/json',
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({
                        "attendance_id": attendanceId,
                        "admin_id": decoded_data.user.admin_id,
                        "subject": subject,
                        "message": message,
                        "tokens": _data
                    })
                })
                    .then(async response => {
                        try {
                            if (response.status !== 200) {
                                console.log("err")
                            } else {
                                const data = await response.json()
                            }
                        } catch (err) {
                            console.log(err);
                        };
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }
            displayAttendance()

        })

        setAdminRespondModal(false)
    }

    // display the attendance list
    const displayAttendance = async () => {

        const dd = moment(date).format("YYYY-MM-DD")
        console.log(dd);
        const URL = window.location.href.startsWith("http://localhost")
            ? "http://localhost:5000/api/attendance/history/id/date"
            : " /api/attendance/history/id/date";
        await fetch(URL, {
            method: 'POST',
            cache: 'no-cache',
            headers: {
                "Accept": 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                "emp_id": id,
                "date": dd,
                "tokens": _data
            })
        })
            .then(async response => {
                try {
                    if (response.status !== 200) {
                        console.log("err")
                    } else {
                        const data = await response.json()
                        setAttendanceData(data.data);
                    }
                } catch (err) {
                    console.log(err);
                };
            })
            .catch(err => {
                console.log(err);
            });
    }


    useEffect(() => {
        displayAttendance()
    }, [])

    return (
        <>

            {/* open activity modal View */}
            <div>
                <Modal open={activityModal} onClose={() => setActivityModal(false)}>
                    <Box sx={style}>
                        <div className="leave-reason">{activity}</div>
                    </Box>
                </Modal>
            </div>

            {/* Attendance Admin response */}
            <Modal open={adminResponseModal} onClose={() => setAdminRespondModal(false)}>
                <Box sx={style}>
                    <center>
                        <form onSubmit={handleResponseSubmit}>
                            <StyTextFieldLeave
                                value={subject}
                                label="Subject."
                                size="small"
                                rows={1}
                                variant="outlined"
                                multiline
                                placeholder="Subject..."
                                onChange={(e) => setSubject(e.target.value)}
                                required
                                fullWidth
                            />
                            <StyTextFieldLeave
                                value={message}
                                label="Response to Activity."
                                size="small"
                                rows={3}
                                variant="outlined"
                                multiline
                                placeholder="Response..."
                                onChange={(e) => setMessage(e.target.value)}
                                required
                                fullWidth
                            />
                            <div className="leave-btn">
                                <YearlyBtn
                                    type="submit"
                                    sx={{
                                        marginRight: "7px",
                                    }}
                                    variant="outlined"
                                >
                                    Submit
                                </YearlyBtn>
                                <DltBtn
                                    variant="outlined"
                                    color="error"
                                    onClick={() => setAdminRespondModal(false)}
                                >
                                    Cancel
                                </DltBtn>
                            </div>
                        </form>
                    </center>
                </Box>
            </Modal>



            <tr colSpan="6" className="expandableTableHeader">
                <StyTableCell>Sign In time</StyTableCell>
                <StyTableCell>Sign Out Time</StyTableCell>
                <StyTableCell>Distance In (mtrs)</StyTableCell>
                <StyTableCell>Distance Out (mtrs)</StyTableCell>
                <StyTableCell>Time of Work</StyTableCell>
                <StyTableCell>Activity</StyTableCell>
                <StyTableCell>Respond</StyTableCell>
            </tr>


            {attendanceData.map((attendance, index) => (
                <TableRow className="expandableTableValue"
                    key={index}
                    sx={{
                        "&:last-child td, &:last-child th": {
                            border: 0,
                        },
                    }}
                >
                    <TableCell align="center">
                        {attendance.in_time ? attendance.in_time : "-"}
                    </TableCell>
                    <TableCell align="center">
                        {attendance.out_time ? attendance.out_time : "-"}
                    </TableCell>
                    <TableCell align="center">
                        {attendance.distance_in ? attendance.distance_in : "-"}
                    </TableCell>
                    <TableCell align="center">
                        {attendance.distance_out ? attendance.distance_out : "-"}
                    </TableCell>
                    <TableCell align="center">
                        {attendance.total_time ? attendance.total_time : "-"}
                    </TableCell>
                    <TableCell align="center">
                        <YearlyBtn
                            size="small"
                            variant="outlined"
                            sx={{ marginRight: "15px" }}
                            onClick={() => {
                                setActivity(attendance.activity);
                                setActivityModal(true)
                            }}
                        >
                            View
                        </YearlyBtn>
                    </TableCell>
                    <TableCell align="center">
                        <YearlyBtn
                            disabled={attendance.response_status == 0 ? false : true}
                            size="small"
                            variant="outlined"
                            sx={{ marginRight: "15px" }}
                            onClick={() => {
                                setAttendanceId(attendance._id)
                                setAdminRespondModal(true)
                            }}
                        >
                            {attendance.response_status == 0 ? "Respond" : "Responded"}
                        </YearlyBtn>
                    </TableCell>
                </TableRow>
            ))}
        </>
    )
}

export default ViewAttendance;